import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { NavBottomComponent } from './components/navigation/nav-bottom.component';
import { NavTopComponent } from './components/navigation/nav-top.component';

import { PageComponent } from './components/views/pages/page.component';
import { LoginComponent } from './components/views/login/login.component';
import { PageNotFoundComponent } from './components/views/page-not-found/page-not-found.component';

@NgModule({
  declarations: [
    AppComponent,
    NavBottomComponent,
    NavTopComponent,
    PageComponent,
    PageNotFoundComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})

export class AppModule { }


